.header {
	height: rem(711);
	background: url(../img/header_bg.png) no-repeat bottom center;
	background-size: cover;

	@include min {
		height: auto;
	}

	@include min540 {
		background: url(../img/header_bg_smart.png) no-repeat top center;
		background-size: cover;
	}

	&__top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		padding-top: rem(35);
		padding-bottom: rem(100);

		@include min540 {
			padding-top: rem(20);
			padding-bottom: rem(36);
		}

		&-center {
			max-width: rem(385);

			@include min {
				display: none;
			}

			p {
				text-align: center;
				font-size: rem(14);
				line-height: rem(20);
				color: white;
			}
		}

		&-right {

			@include min540 {
				display: none;
			}

			.top {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(8);

				img {
					margin-right: rem(15);
				}


				b {
					font-weight: var(--fw-extra);
					font-size: rem(24);
					line-height: rem(30);
					color: white;
				}
			}

			.bottom {
				display: flex;
				justify-content: center;
				a {
					color: white;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;

		.left {
			flex: 0 0 rem(735);
			max-width: rem(735);

			@include min1160 {
				flex: 0 0 rem(600);
				max-width: rem(600);
			}

			@include min {
				flex: 0 0 100%;
				max-width: 100%;
			}

			h1 {
				font-weight: bold;
				font-size: rem(42);
				line-height: rem(55);
				color: white;
				margin-bottom: rem(20);

				@include min1160 {
					font-size: rem(32);
					line-height: rem(45);
				}

				@include min {
					font-size: rem(24);
					line-height: rem(24);
					margin-bottom: rem(24);
				}
			}

			p {
				font-weight: var(--fw-medium);
				font-size: rem(24);
				line-height: rem(30);
				color: white;

				@include min1160 {
					font-size: rem(20);
					line-height: rem(26);
				}

				@include min {
					font-size: rem(14);
					line-height: rem(16);
				}
			}
		}

		.right {
			position: relative;
			max-width: rem(400);

			@include min1160 {
				max-width: rem(270);
			}

			@include min {
				max-width: 100%;
				flex: 0 0 100%;

				display: flex;
				justify-content: flex-end;
			}

			img {
				position: relative;
				left: rem(-40);
				top: rem(-40);

				@include min1160 {
					left: 0;
				}

				@include min {
					top: 0;
					left: auto;
					right: rem(-50);
				}
			}

			.text-block {
				position: absolute;
				bottom: rem(120);
				right: 0;
				width: rem(272);
				background-color: white;
				border-radius: rem(10);
				padding: rem(15) 0;
				box-shadow: 5px 5px 33px rgba(0, 0, 0, 0.15);
				display: flex;
				flex-direction: column;
				align-items: center;

				@include min1160 {
					bottom: rem(40);
				}

				@include min {
					right: rem(50);
					bottom: rem(70);
				}

				@include min540 {
					right: auto;
					left: rem(-12);
					bottom: rem(220);
					border-top-left-radius: 0;
					border-bottom-left-radius: 0;
					box-shadow: none;

					padding: rem(10);
					width: auto;
				}

				b {
					margin-bottom: rem(5);
					background: var(--blue-gradient);
					background-clip: text;
					-webkit-text-fill-color: transparent;
					font-size: rem(24);

					@include min540 {
						font-size: rem(18);
					}
				}

				p {
					color: #393939;
					font-size: rem(18);
					font-weight: var(--fw-medium);

					@include min540 {
						font-size: rem(14);
					}
				}
			}
		}
	}
}