.popup {
	z-index: 100;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.25);

	display: none;
	justify-content: center;
	align-items: center;

	&__overlay {
		width: 100%;
		max-width: rem(1200);

		@include min1200 {
			max-width: 100%;
			width: calc(100% - 24px);
		}

		.contact_form {
			top: auto;
			margin-bottom: 0;
			background-color: white;
			border-radius: rem(10);
			padding: rem(48) rem(53);

			@include min1200 {
				padding: rem(24) rem(20);
			}

			.close {
				position: absolute;
				top: rem(20);
				right: rem(20);

				transition: all 300ms ease;

				&:hover {
					opacity: .7;
				}
			}

			h2,h3 {
				font-size: rem(42);
				line-height: rem(55);
				color: #2A5E8D;
				margin-bottom: rem(46);
				text-transform: uppercase;

				@include min1200 {
					font-size: rem(32);
					line-height: rem(45);
					margin-bottom: rem(32);
				}

				@include min {
					font-size: rem(18);
					line-height: rem(20);
					margin-bottom: rem(17);
				}

				@include min540 {
					max-width: 90%;
				}
			}

			&__wrap {
				padding: 0;

				@include min {
					display: flex;

					.button {
						margin-left: auto;
						margin-right: auto;
					}
				}

				.field-set {
					&:nth-child(3) {
						flex: 0 0 rem(235);
						max-width: rem(235);

						@include min {
							flex: 0 0 100%;
							max-width: 100%;
						}
					}
				}
			}
		}
	}
}

.popup.active {
	display: flex;
}