.conference {
	overflow: hidden;
	margin-bottom: rem(65);

	@include min {
		margin-bottom: rem(56);
	}

	.section-header {
		margin-bottom: rem(37);

		@include min {
			margin-bottom: rem(24);
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
	}

	&__item {
		background-color: white;
		padding: rem(30) rem(38);
		flex: 0 0 rem(560);
		max-width: rem(560);

		@include min1200 {
			flex: 0 0 100%;
			max-width: 100%;
		}

		@include min768 {
			padding: rem(16);
		}

		&:first-child {
			margin-right: rem(20);

			height: rem(558);

			@include min1200 {
				height: auto;
				margin-bottom: rem(14);
			}
		}

		.top {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			&__info {
				display: flex;
				flex-direction: column;

				flex: 0 0 rem(297);
				max-width: rem(297);
				
				@include min540 {
					flex: 0 0 rem(192);
					max-width: rem(192);
				}

				@include min340 {
					flex: 0 0 rem(172);
					max-width: rem(172);
				}

				.title {
					font-size: rem(24);
					margin-bottom: rem(5);
					font-weight: var(--fw-extra);
					line-height: rem(30);
					background: var(--blue-gradient);
					background-clip: text;
					-webkit-text-fill-color: transparent;

					@include min540 {
						font-size: rem(18);
					}
				}

				p {
					font-weight: var(--fw-extra);
					color: var(--c-text);

					@include min540 {
						font-size: rem(12);
						line-height: rem(14);
					}
				}
			}

			&__image {
				@include min540 {
					width: rem(80);
					height: rem(80);
				}
			}
		}

		.bottom {
			margin-top: rem(15);
			max-width: rem(343);

			@include min1200 {
				max-width: 80%;
			}

			p {
				line-height: rem(25);
				color: var(--c-text);
				margin-bottom: rem(20);

				@include min540 {
					font-size: rem(12);
					line-height: rem(14);
					margin-bottom: rem(12);
				}

				&:last-of-type {
					margin-bottom: 0;
				}
			}
		}
	}

	.flex.flex-col {
		flex: 0 0 rem(560);
		max-width: rem(560);
		height: rem(558);
		align-self: baseline;

		@include min1200 {
			flex: 0 0 100%;
			max-width: 100%;
			height: auto;
		}

		.conference__item {
			margin-bottom: rem(20);
			max-height: rem(270);
			margin-right: 0;

			&:last-child {
				margin-bottom: 0;
			}
		}

		
	}
}