.preview {
	background: url(../img/section_2_bg.jpg) no-repeat top center;
	background-size: cover;
	padding: rem(52) 0;

	margin-bottom: rem(65);

	@include min768 {
		padding: rem(24) 0;
		margin-bottom: rem(40);
	}

	@include min540 {
		background: url(../img/section_2_bg_smart.jpg) no-repeat top center;
		background-size: cover;
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	&__text {
		flex: 0 0 rem(735);
		max-width: rem(735);
		@include min {
			flex: 0 0 100%;
			max-width: 100%;
		}
		h2,h3 {
			font-size: rem(42);
			line-height: rem(55);
			text-transform: uppercase;
			color: white;
			font-weight: bold;
			margin-bottom: rem(20);

			@include min {
				font-size: rem(24);
			}
		}

		p {
			font-weight: bold;
			font-size: rem(24);
			line-height: rem(30);
			color: white;

			@include min {
				font-size: rem(14);
				line-height: rem(16);
			}
		}
	}

	&__button {
		margin-left: rem(52);

		@include min {
			margin-left: 0;
			margin-top: rem(30);
		}

		@include min540 {
			width: 100%;
			display: flex;
			justify-content: center;
		}

	}
}