.section-header {
	h1,h2,h3 {
		color: var(--c-title);
		text-transform: uppercase;
		font-size: rem(42);
		line-height: rem(55);
		font-weight: var(--fw-extra);
		text-align: center;
		margin-bottom: rem(12);


		@include min {
			font-size: rem(32);
			line-height: rem(45);
		}

		@include min540 {
			font-size: rem(24);
			line-height: rem(55);
			margin-bottom: 0;
		}
	}

	p {
		text-align: center;
		font-weight: var(--fw-extra);
		color: var(--c-text);
		font-size: rem(24);
		line-height: rem(30);

		@include min {
			font-size: rem(20);
			line-height: rem(26);
		}

		@include min540 {
			font-size: rem(14);
			line-height: rem(30);
		}
	}
}

.what {

	margin-bottom: rem(34);

	@include min540 {
		margin-bottom: rem(27);
	}

	.section-header {
		margin-bottom: rem(45);

		@include min540 {
			margin-bottom: rem(24);
		}
	}


	&__items {
		display: flex;
		flex-wrap: wrap;


	}

	&__item {
		position: relative;
		overflow: hidden;
		width: calc((100% - 40px) / 2);
		margin-right: rem(20);
		margin-bottom: rem(20);

		padding: rem(36) rem(20) rem(27) rem(30);
		background-color: white;

		display: flex;
		flex-wrap: wrap;

		@include min {
			width: 100%;
			margin-right: 0;

			padding: rem(17);
		}

		@include min768 {
			height: rem(94);
		}

		@include min540 {
			margin-bottom: rem(5);
		}

		&:nth-child(2n) {
			margin-right: 0;
		}


		.left {
			display: flex;
			flex-direction: column;

			.title {
				font-size: rem(24);
				line-height: rem(55);
				font-weight: var(--fw-extra);
				color: var(--c-text);
				text-transform: uppercase;

				@include min768 {
					font-size: rem(18);
					line-height: rem(18);
				}
			}

			p {
				line-height: rem(25);
				color: var(--c-text);

				@include min768 {
					font-size: rem(12);
					line-height: rem(14);

					height: 100%;
					display: flex;
					align-items: center;
				}
			}
		}

		.right {
			position: absolute;
			bottom: rem(-5);
			right: rem(-20);
		}
	}

	.item_1 {
		.left {
			flex: 0 0 rem(396);
			max-width: rem(396);

			@include min768 {
				flex: 0 0 rem(209);
				max-width: rem(209);
			}
		}

		.right {
			right: rem(-20);
			bottom: rem(-5);

			@include min768 {
				width: rem(113);
				height: rem(80);
			}
		}
	}

	.item_2 {
		.left {
			flex: 0 0 rem(320);
			max-width: rem(320);

			@include min768 {
				flex: 0 0 rem(183);
				max-width: rem(183);
			}
		}

		.right {
			@include min768 {
				width: rem(102);
				height: rem(83);

				right: 0;
			}
		}
	}

	.item_3 {
		.left {
			flex: 0 0 rem(360);
			max-width: rem(360);

			@include min768 {
				flex: 0 0 rem(217);
				max-width: rem(217);
			}
		}

		.right {
			right: rem(5);

			@include min768 {
				width: rem(92);
				height: rem(68);
			}
		}
	}

	.item_4 {
		.left {
			flex: 0 0 rem(202);
			max-width: rem(202);

			// @include min768 {
			// 	.title {
			// 		margin-bottom: rem(12);
			// 	}
			// }
		}

		.right {
			right: rem(42);

			@include min768 {
				width: rem(87);
				height: rem(80);
				right: rem(12);
			}
		}
	}

	.item_5 {
		.left {
			flex: 0 0 rem(396);
			max-width: rem(396);

			@include min768 {
				flex: 0 0 rem(217);
				max-width: rem(217);
			}
		}

		.right {
			right: 0;

			@include min768 {
				width: rem(88);
				height: rem(78);
				right: rem(12);
				bottom: 0;
			}
		}
	}

	.item_6 {
		.left {
			flex: 0 0 rem(347);
			max-width: rem(347);

			@include min768 {
				flex: 0 0 rem(210);
				max-width: rem(210);
			}
		}

		.right {
			bottom: 27%;
			right: rem(20);

			@include min768 {
				width: rem(90);
			}
		}
	}

	.item_7 {
		.left {
			flex: 0 0 rem(360);
			max-width: rem(360);

			@include min768 {
				flex: 0 0 rem(242);
				max-width: rem(242);
			}
		}

		.right {
			right: 0;

			@include min768 {
				width: rem(167);
				height: rem(94);
			}
		}
	}

	.item_8 {
		.left {
			flex: 0 0 rem(347);
			max-width: rem(347);

			@include min768 {
				flex: 0 0 rem(234);
				max-width: rem(234);
			}
		}

		.right {
			right: rem(20);

			@include min768 {
				right: rem(12);
				width: rem(90);
				height: rem(78);
				bottom: 0;
			}
		}
	}
}