.excursion {
	margin-bottom: rem(118);

	@include min {
		margin-bottom: rem(52);
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	&__left {
		margin-right: rem(42);

		flex: 0 0 rem(294);
		max-width: rem(294);

		@include min1200 {
			flex: 0 0 25%;
			max-width: 25%;
		}

		@include min768 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;

			display: flex;
			justify-content: center;
		}
	}

	&__right {
		flex: 0 0 rem(736);
		max-width: rem(736);

		padding-top: rem(50);

		@include min1200 {
			flex: 0 0 67%;
			max-width: 67%;
		}

		@include min768 {
			flex: 0 0 100%;
			max-width: 100%;
		}

		.title {
			font-size: rem(42);
			font-weight: bold;
			line-height: rem(55);
			color: var(--c-text);
			margin-bottom: rem(37);

			@include min {
				font-size: rem(32);
				line-height: rem(45);
				margin-bottom: rem(28);
			}

			@include min768 {
				font-size: rem(18);
				line-height: rem(20);
				max-width: rem(313);
				margin-left: auto;
				margin-right: auto;
			}
		}

		.flex {
			@include min768 {
				justify-content: center;
			}
		}

		.button {
			align-self: flex-start;
		}
	}
}