.footer {
	padding-bottom: rem(105);

	.section-header {
		margin-bottom: rem(52);

		@include min {
			margin-bottom: rem(20);
			max-width: rem(250);
			margin-left: auto;
			margin-right: auto;

			h2,h3 {
				font-size: rem(24);
				line-height: rem(28);
			}
		}
	}

	&__content {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;

		@include min {
			justify-content: flex-start;
		}

		.smart {
			display: none;

			@include min {
				display: block;
				border-radius: 0;
				width: 100%;
				flex: 0 0 100%;
				height: auto;

				margin-bottom: rem(20);
			}
		}

		img {
			margin-right: rem(92);
			border-radius: 50%;
			flex: 0 0 rem(250);
			width: rem(250);
			height: rem(250);

			@include min {
				margin-right: 0;
				display: none;
			}
		}

		ul {
			li {
				margin-bottom: rem(34);

				font-size: rem(24);
				line-height: rem(55);
				font-weight: bold;
				color: var(--c-text);

				@include min {
					margin-bottom: 0;
					font-size: rem(14);
					line-height: rem(16);
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}